<template>
  <div class="todo-item" v-bind:class="{ 'is-complete': itemInfo.completed }">
    <p>
      <input
        type="checkbox"
        v-on:change="changeCompletion"
        v-bind:checked="itemInfo.completed"
      />
      {{ itemInfo.title }}
      <button @click="$emit('del-todo', itemInfo.id)" class="del">x</button>
    </p>
  </div>
</template>

<script>
export default {
  name: "TodoItem",
  props: ["itemInfo"],
  methods: {
    changeCompletion() {
      this.$emit("change-completion", this.itemInfo.id);
    },
  },
};
</script>

<style scoped>
.todo-item {
  background: #f4f4f4;
  padding: 10px;
  border-bottom: 1px #ccc dotted;
}
.is-complete {
  text-decoration: line-through;
}
.del {
  background: #ff0000;
  color: #fff;
  border: none;
  padding: 5px 9px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
}
</style>